.foto {
  padding-top: 110px;
  padding-bottom: 110px;


  &__header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    @include small-tablet {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 96px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 72px;
    line-height: 88px;
    color: var(--dark-color);
    text-transform: uppercase;

    @include small-tablet {
      font-size: 48px;
      line-height: 52px;
      margin-bottom: 50px;
    }

    @include mobile {
      font-size: 32px;
    }
  }


  &__arrows-btn {
    margin-left: auto;
    display: flex;

    @include small-tablet {
      margin-left: 0;
      padding-bottom: 30px;
    }
  }

  &__content-box {
    padding: 32px;
    border: 1px solid #E4E4E4;


    @include mobile {
      padding: 16px;
    }
  }
}

.arrows-btn:not(:last-child) {
  margin-right: 20px;
}

.foto-slider {
  width: 100%;
  height: 100%;
  overflow: hidden;
}



.swiper-slide {
  width: 100%;
  height: 100%;



  .image {
    padding-bottom: 15px;
    width: 603px;
    height: 723px;
    width: 100%;
    height: 100%;
  }
}

.foto-descr {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: var(--dark-color);

  @include small-tablet {
    font-size: 12px;

  }
}

.arrows-btn {
  cursor: pointer;
}


