.header {
  padding: 70px 0 25px;

  @include tablet {
    padding: 24px 0;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      background: #e4e4e4;
      width: 100%;
      height: 1px;
      bottom: -25px;
    }
  }

  &__header-logo {
    margin-right: 58px;

    @include hight-tablet {
      margin-right: 0px;
      margin-left: 40px;
    }

    @include tablet {
      width: 70%;
    }
  }

  &__phone-modal {
    margin-left: auto;

    @include hight-tablet {
      // display: none;
    }
  }

  &__btn-modal {
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    color: var(--dark-color);
    padding: 8px 24px;
    background-color: #e4e4e4;
    border-radius: 100px;
    margin-right: 32px;

    @include mobile {
      display: none;
    }
  }

  &__phone {
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    font-size: 15px;
    line-height: 100%;
    color: var(--dark-color);

    span {
      display: block;

      @include tablet {
        display: none;
      }
    }

  }

  &__phone-link {
    display: block;
    padding-top: 5px;
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
    font-size: 18px;
    line-height: 100%;
    color: var(--dark-color);

    @include tablet {
      background-image: url("../img/phone-icon.svg");
      background-repeat: no-repeat;
      background-size: cover;
      width: 18px;
      height: 18px;
      padding: 10px;
      cursor: pointer;
    }
  }
}


