.review {
  padding-top: 110px;
  padding-bottom: 110px;
  position: relative;

  @include small-tablet {
    padding: 50px 0;
  }

  &__title {
    margin: 0;
    margin-bottom: 96px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 72px;
    line-height: 88px;
    text-transform: uppercase;
    color: var(--dark-color);

    @include small-tablet {
      font-size: 58px;
      line-height: 60px;
    }

    @include mobile {
      font-size: 32px;
      margin-bottom: 40px;
    }
  }

}

.swiper-slide-active {
  width: 1366px;
  height: 638px;
  transform: scale(1);
  padding: 130px 0;

}


.gallery-top {
  width: 100%;
  height: 738px;
  margin-left: auto;
  margin-right: auto;

  @include hight-tablet {
    max-height: 555px;
  }

  @include mobile {
    max-height: 333px;
  }
}

.swiper-slide {
  background-size: cover;
  background-position: center;
}

// .gallery-top {
//   height: 80%;
//   width: 100%;
// }

.gallery-thumbs {
  height: 20%;
  box-sizing: border-box;
  padding: 50px 0;

}

.swiper-wrapper .swiper-slide {
  padding: 0;
}

.gallery-thumbs .swiper-slide {
  width: 25%;
  height: 100%;
  min-height: 180px;
  opacity: 0.4;
  max-width: 100%;
}

.gallery-thumbs .swiper-slide-thumb-active {
  opacity: 1;
}
