.set {
  padding: 80px 0 110px;

  @include tablet {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;

    @include midle-tablet {
      flex-direction: column-reverse;
      align-items: center;
    }
  }

  &__content {
    flex: 0 1 50%;

    @include midle-tablet {
      max-width: 670px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 40px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 72px;
    line-height: 88px;
    text-transform: uppercase;
    color: var(--dark-color);

    span {
      color: var(--green-color);
    }

    @include hight-tablet {
      font-size: 58px;
    }

    @include tablet {
      font-size: 46px;
    }

    @include mobile {
      font-size: 32px;
      line-height: 140%;
      margin-bottom: 20px;
    }
  }

  &__descr {
    max-width: 420px;
    margin: 0;
    margin-bottom: 60px;
    font-size: 24px;
    line-height: 33px;
    color: var(--dark-color);

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
  }
}
