.btn {
  font-family: "Open Sans", sans-serif;
    font-weight: 600;
    font-size: 12px;
    line-height: 22px;
    text-transform: uppercase;
    color: var(--light-color);
    padding: 24px 64px;
    background-color: #228749;
    border-radius: 4px;
    transition: background-color 0.3s ease-in-out;

    &:hover {
      background-color: rgba(#228749, 0.9);
    }
}
