.timers {
  padding: 80px 0 110px;

  @include midle-tablet {
    padding: 40px 0 50px;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin: 0;
    margin-bottom: 60px;
    max-width: 650px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 72px;
    line-height: 88px;
    text-transform: uppercase;
    color: var(--dark-color);

    span {
      color: #228749;
    }

    @include midle-tablet {
      font-size: 58px;
      line-height: 60px;
      margin-bottom: 40px;
    }

    @include mobile {
      font-size: 32px;
    }
  }

  &__descr-box {
    margin-left: auto;
    padding-right: 200px;
    padding-bottom: 160px;

    @include midle-tablet {
      padding-right: 0;
      padding-bottom: 60px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 40px;
    max-width: 460px;
    font-weight: 400;
    font-size: 24px;
    line-height: 33px;
    color: var(--dark-color);
  }

  &__timer {
    width: 100%;
  }

  &__items {
    display: flex;
    justify-content: space-evenly;
    max-width: 100%;
    font-size: 144px;
    line-height: 176px;
    text-transform: uppercase;
    color: var(--dark-color);

    @include hight-tablet {
      font-size: 110px;
    }

    @include tablet-880 {
      font-size: 80px;
    }

    @include mobile {
      font-size: 35px;
    }


  }

  &__item-box {
    width: 190px;
    height: 310px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    background: #f9f9f9;
    border: 0.660861px solid #e4e4e4;
    border-radius: 5.28689px;

    @include hight-tablet {
      width: 150px;
      height: 230px;
    }

    @include small-tablet {
      width: 120px;
      height: 180px;
    }

    @include mobile {
      width: 60px;
      height: 120px;
    }

    &:not(:last-child)::after {
      content: ":";
      position: absolute;
      right: -45%;

      @include hight-tablet {
        right: -60px;
      }

      @include tablet {
        right:-43px;
      }

      @include tablet-880 {
        right: -30px;
      }

      @include small-tablet {
        right: -25px;
      }

      @include mobile {
        right: -35px;
      }
    }
  }

  &__item {
    position: relative;
    min-width: 60px;
    margin-left: 10px;
    margin-right: 10px;
    padding-bottom: 15px;
    text-align: center;

    @include hight-tablet {
      padding-bottom: 0;
    }

    &::before {
      content: attr(data-title);
      display: block;
      position: absolute;
      left: 50%;
      bottom: -50%;
      transform: translateX(-50%);
      font-size: 14px;

      @include small-tablet {
        bottom: -40%;
      }

      @include mobile {
        bottom: -20%;
        font-size: 10px;
      }
    }

    &:not(:last-child)::after {
      content: ":";
      position: absolute;
      top: -10px;
      right: -30px;
    }
  }
}
