.door {
  padding: 110px 0 110px;

  @include tablet {
    padding: 50px 0 50px;
  }

  &__title {
    margin: 0;
    margin-bottom: 60px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 72px;
    line-height: 88px;
    text-transform: uppercase;
    color: var(--dark-color);
    text-align: left;

    @include hight-tablet {
      font-size: 58px;
    }

    @include tablet {
      margin-bottom: 20px;
    }

    @include mobile {
      font-size: 32px;

    }
  }

  &__descr {
    display: block;
    max-width: 415px;
    margin: 0;
    margin-bottom: 160px;
    margin-right: 215px;
    margin-left: auto;
    font-weight: 400;
    font-size: 23px;
    line-height: 33px;
    color: var(--dark-color);

    @include tablet {
      max-width: 100%;
      margin-right: 0;
      margin-bottom: 0px;
    }
  }

  &__box {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__text {
    max-width: 467px;
    margin-bottom: 50px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 20px;
    line-height: 140%;
    color: var(--dark-color);
    transition: all 0.5s ease;

    display: none;
  }

  &__btn {
    display: none;
    transition: all 0.5s ease;

    @include mobile {
      padding: 24px 40px;
    }
  }

  &__btn-box {
    display: flex;
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(-50%, 50%);
    z-index: 23123123;

    .arrow-prev {
      position: absolute;
      left: -190px;

      @include mobile {
        left: -140px;
      }
    }

    .arrow-next {
      position: absolute;
      right: -187px;

      @include mobile {
        right: -140px;
      }
    }
  }
}

.door-swiper {
  position: relative;

  .swiper-slide {
    border: none;
  }

  .swiper-slide {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 130px 0;
    transition: all 0.5s ease;
    border: none;

    .image {
      margin-bottom: 50px;
      width: 318px;
      height: 478px;

      @include tablet {
        margin-bottom: 0;
      }
    }

    @include tablet {
      padding: 30px 0;
    }
  }

  .swiper-slide-active {
    transform: scale(1.3);

    @include hight-tablet {
      transform: scale(1.1);
    }

    @include mobile {
      transform: scale(1);
    }
  }
}


.swiper-slide-active .door__btn {
  display: block;
}

.swiper-slide-active .door__text {
  max-width: 300px;
  display: block;
}


.swiper-wrapper {
  display: flex;
  align-items: flex-end;
}
