.advantage {
  padding: 110px 0 80px;

  @include small-tablet {
    padding: 0px;
  }

  &__titles {
    margin: 0;
    margin-bottom: 90px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 72px;
    line-height: 88px;
    text-transform: uppercase;
    color: var(--dark-color);

    @include tablet {
      font-size: 58px;
    }

    @include mobile {
      font-size: 32px;
      margin-bottom: 50px;
    }
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 90px;

    @include midle-tablet {
      gap: 50px;
    }

    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__item {
    max-width: 334px;
  }

  &__image-box {
    margin-bottom: 23px;
  }

  &__title {
    margin: 0;
    margin-bottom: 23px;
    font-family: "Montserrat", sans-serif;
    font-size: 24px;
    line-height: 29px;
    color: var(--dark-color);


  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    color: var(--dark-color);
  }
}
