.footer {
  padding: 60px 0;
  background: #F9F9F9;

  @include tablet {
    padding: 30px 0;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 32px;
    border-bottom: 1px solid #E4E4E4;

    @include tablet {
      flex-direction: column;
    }
  }

  &__item {
    padding-left: 40px;
    max-width: 530px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: var(--dark-color);
    position: relative;

    &:nth-child(1)::before {
      content: '';
      background-image: url(../img/geolocation.png);
      background-repeat: no-repeat;
      background-size: cover;
      width: 14px;
      height: 20px;
      position: absolute;
      left: 0;
      top: 3px;
    }

    &:nth-child(2) {
      background-image: url(../img/phone.png);
      background-repeat: no-repeat;
    }

    &:nth-child(3) {
      background-image: url(../img/mailto.png);
      background-repeat: no-repeat;
    }

    @include tablet {
      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }
  }

  &__item-link{
    text-decoration: underline;
    color: var(--green-color);
  }

  &__link {
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: var(--dark-color);
  }

  &__copyright {
    padding-top: 32px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include hight-tablet {
      flex-direction: column;
    }
  }

  &__copyright-link {
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    text-decoration: underline;
    color: var(--green-color);

    @include hight-tablet {
      margin-bottom: 20px;
    }
  }

  &__descr {
    margin: 0;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: var(--dark-color);

    @include hight-tablet {
      margin-bottom: 20px;
    }
  }
}

.social {
  display: flex;
  align-items: center;


  &__item {
    &:not(:last-child) {
      margin-right: 16px;
    }
  }
}
