.madein {
  padding-bottom: 80px;

  @include tablet {
    padding-bottom: 40px;
  }

  &__wrapper {
    display: flex;

    @include tablet {
      flex-direction: column-reverse;
    }
  }

  &__content {
    max-width: 350px;
    width: 100%;
    padding: 60px;
    position: relative;

    &::before {
      content: '';
      width: 100%;
      height: 1px;
      background: #E4E4E4;
      position: absolute;
      top: 0;
      left: 0;
    }

    &::after {
      content: '';
      width: 100%;
      height: 1px;
      background: #E4E4E4;
      position: absolute;
      bottom: 5px;
      left: 0;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  &__item {
    display: flex;
    align-items: center;
    position: relative;

    &:not(:last-child) {
      padding-bottom: 41px;
      margin-bottom: 41px;
      &::before {
        content: '';
        width: 100%;
        height: 1px;
        background: #E4E4E4;
        position: absolute;
        bottom: 0;
      }
    }

    img {
      width: 100%;
      max-width: inherit;
    }
  }

  &__text {
    margin: 0;
    margin-left: 20px;
  }


  &__content-image {
    max-width: 100%;
    height: 100%;
    position: relative;

    @include tablet {
      height: 100%;
      &::after {
        content: '';
        background-image: url('../img/madein-img-size.png');
        background-size: cover;
        width: 200px;
        height: 380px;
        position: absolute;
        right: 0;
        bottom: 0;
      }
    }

    @include midle-tablet {
      &::after {
        width: 160px;
        height: 280px;
        height: 80%;
      }
    }

    @include small-tablet {
      &::after {
        display: none;
      }
    }
  }

  &__madein-image {
    max-width: 100%;
    position: absolute;
    right: -2px;
    bottom: 4px;

    @include tablet {
      display: none;

    }

    @include midle-tablet {

    }
  }
}
