.departure {
  padding: 110px 0 80px;

  @include midle-tablet {
    padding: 50px 0 40px;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__name-box {
    display: flex;
    flex-direction: column;
    padding-right: 200px;
    margin-bottom: 120px;

    @include midle-tablet {
      padding-right: 0;
    }

  }

  &__title {
    margin: 0;
    margin-bottom: 60px;
    max-width: 890px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 72px;
    line-height: 88px;
    color: var(--dark-color);
    text-transform: uppercase;

    @include midle-tablet {
      font-size: 58px;
      line-height: 60px;
      margin-bottom: 40px;
    }

    @include mobile {
      font-size: 32px;
    }
  }

  &__descr {
    margin: 0;
    max-width: 470px;
    margin-left: auto;
    font-weight: 400;
    font-size: 24px;
    line-height: 33px;
    color: var(--dark-color);
  }

  &__list {
    position: relative;
    &::before {
      content: "";
      width: 100%;
      height: 1px;
      background: #e4e4e4;
      position: absolute;
    }

    &::after {
      content: "";
      width: 100%;
      height: 1px;
      background: #e4e4e4;
      position: absolute;
    }
  }

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 32px 0;
    position: relative;

    &:not(:last-child) {
      &::after {
        content: "";
        width: 100%;
        height: 1px;
        background: #e4e4e4;
        position: absolute;
        bottom: 0;
      }
    }

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__item-descr {
    max-width: 510px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    color: var(--dark-color);

    @include midle-tablet {
      font-size: 20px;
      padding-left: 20px;
    }

    @include small-tablet {
      margin-bottom: 20px;
      padding-left: 0;
    }
  }

  &__btn {
    background: #e4e4e4;
    border-radius: 4px;
    padding: 16px 81px;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    text-transform: uppercase;
    color: var(--dark-color);
    white-space: nowrap;

    @include midle-tablet {
      padding: 14px 40px;
    }
  }
}
