.feedback {
  padding-top: 110px;
  padding-bottom: 110px;

  @include small-tablet {
    padding: 0;
  }

  &__title {
    margin: 0;
    margin-bottom: 60px;
    font-family: "Montserrat", sans-serif;
    max-width: 800px;
    font-weight: 600;
    font-size: 72px;
    line-height: 88px;
    text-transform: uppercase;
    color: var(--dark-color);

    @include small-tablet {
      font-size: 58px;
      line-height: 60px;
    }

    @include mobile {
      font-size: 32px;
      margin-bottom: 30px;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 72px;
    max-width: 750px;
    margin-left: auto;
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    font-size: 36px;
    line-height: 49px;
    color: var(--dark-color);

    @include mobile {
      font-size: 24px;
      margin-bottom: 30px;
    }
  }
}

.form {
 margin-left: auto;
 margin-right: auto;

  max-width: 667px;
  height: 488px;
  width: 100%;

  &__descr {
    margin: 0;
    margin-bottom: 60px;
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    color: var(--dark-color);

    a {
      color: #228749;
      text-decoration: underline;
    }
  }
}
