.contact {
  padding-top: 110px;
  padding-bottom: 60px;

  @include midle-tablet{
    padding-top: 50px;
    padding-bottom: 30px;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    @include hight-tablet {
      flex-direction: column;
    }
  }

  &__maps {
    margin-right: 30px;
    width: 667px;

    @include hight-tablet {
      width: 100%;
      margin-bottom: 30px;
    }
  }

  &__map {
    @include hight-tablet {
      width: 100%;
    }
  }

  &__address {
    max-width: 667px;
    min-width: 315px;
  }

  &__accordion-box {
    padding-bottom: 32px;
    max-width: 667px;
    min-width: 315px;
  }

  &__accordion {
    max-width: 667px;

    margin-bottom: 10px;
    background: #f9f9f9;
    cursor: pointer;
  }

  &__accordion[open] .contact__accordion-icon {
    transform: rotate(45deg);
  }

  &__accordion-control {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    padding: 24px;
  }

  &__accordion-control::-webkit-details-marker {
    display: none;
  }

  &__accordion-city {
    margin: 0;
    font-weight: 400;
    font-size: 24px;
    line-height: 33px;
    color: var(--dark-color);
  }

  &__accordion-station {
    margin: 0;
    font-weight: 400;
    font-size: 24px;
    line-height: 33px;
    color: var(--grey-color);
  }

  &__accordion-icon {
    position: relative;
    display: inline-block;
    width: 30px;
    height: 30px;
    border-radius: 100%;
    background-color: #bfbfbf;
  }

  &__accordion-icon::before {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    width: 50%;
    height: 1px;
    background-color: var(--dark-color);
    transform: translate(-50%, -50%);
  }

  &__accordion-icon::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    width: 1px;
    height: 50%;
    background-color: var(--dark-color);
    transform: translate(-50%, -50%);
  }

  &__accordion-content {
    padding: 24px;
    font-weight: 400;
    font-size: 24px;
    line-height: 33px;
    color: var(--dark-color);
    border-top: 1px solid #e4e4e4;
  }

  &__address-town {
    padding-top: 32px;
    border-top: 1px solid #e4e4e4;
  }

  &__address-town-title {
    margin-bottom: 26px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: var(--dark-color);
  }

  &__address-town-title-span {
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    color: var(--dark-color);
  }

  &__accordion-control-btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    border-top: 1px solid #e4e4e4;
    padding: 24px 24px 24px 0;
  }

  &__accordion-item-span {
    font-weight: 600;
    font-family: "Montserrat", sans-serif;
    font-size: 18px;
    line-height: 22px;
    color: var(--dark-color);

    @include mobile {
      font-size: 14px;
      max-width: 250px;
    }
  }

  // &__accordion-item {
  //  background-color: #F9F9F9;
  // }

  .open .contact__address-span {
    padding: 24px 0;
    padding-top: 0;
  }

  &__accordion-item-content {
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    // padding: 0 24px;
    will-change: max-height;
    transition: all 0.3s ease-out;
    box-sizing: content-box;
  }

  // .open .contact__accordion-item-content {
  //   display: block;
  // }

  &__accordion-item-span-icon {
    position: relative;
    display: inline-block;
    width: 30px;
    height: 30px;
    border-radius: 100%;
    background-color: #bfbfbf;
  }

  &__accordion-item-span-icon::before {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    width: 50%;
    height: 1px;
    background-color: var(--dark-color);
    transform: translate(-50%, -50%);
  }

  &__accordion-item-span-icon::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    width: 1px;
    height: 50%;
    background-color: var(--dark-color);
    transform: translate(-50%, -50%);
  }

  .contact__accordion-item-span-icon {
    transition: transform 0.3s ease-out;
  }

  .open .contact__accordion-item-span-icon {
    transform: rotate(45deg);
  }

  .open .contact__accordion-item-content {
    opacity: 1;
    // padding: 24px;
  }

  &__list {
    border-top: 1px solid #e4e4e4;
  }

  &__item {
    padding: 24px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #e4e4e4;

    @include mobile {
      justify-content: space-evenly;
    }
  }

  &__mode {
    font-weight: 400;
    font-size: 18px;
    line-height: 100%;
    color: var(--dark-color);
  }

  &__mode-cursor {
    font-weight: 600;
    color: var(--dark-color);
  }

  &__mode-link {
    font-weight: 600;
    font-size: 18px;
    line-height: 100%;
    color: var(--green-color);
  }

  &__content-box {
    display: flex;
    flex-direction: column;
  }

  &__img-box {
    padding-top: 22px;
    padding-bottom: 32px;

    img {
      margin-right: 16px;
    }
  }

  &__btn-box {
    padding-bottom: 32px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

  }

  &__btn-grey {
    width: 100%;
    margin-bottom: 20px;
    padding: 16px 106px;
    background: #e4e4e4;
    border-radius: 4px;
    color: var(--dark-color);
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    text-transform: uppercase;
  }

  &__btn-green {
    width: 100%;
    padding: 16px 62px;
  }

  &__scroll-up {
    padding-top: 52px;
    display: flex;
    justify-content: flex-end;
    align-items: center;

  }

  &__text-up {
    padding-right: 16px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    text-transform: uppercase;
    color: var(--green-color);
  }

  &__arrow-up {
    width: 48px;
    height: 48px;
    background-color: var(--green-color);
    border-radius: 4px;
    position: relative;

    &::before {
      content: '';
      background-image: url(../img/arrow-up.png);
      background-size: cover;
      width: 18px;
      height: 18px;
      position: absolute;
      left: 50%;
      bottom: 50%;
      transform: translate(-50%, 50%);
    }
  }
}
