.order {
  padding: 80px 0 110px;

  @include midle-tablet {
    padding: 40px 0 50px;
  }

  &__title {
    max-width: 1100px;
    margin: 0;
    margin-bottom: 70px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 72px;
    line-height: 88px;
    text-transform: uppercase;
    color: var(--dark-color);

    @include midle-tablet {
      font-size: 58px;
      line-height: 60px;
    }

    @include mobile {
      font-size: 32px;
      margin-bottom: 40px;
    }
  }

  &__wrapper {
    display: flex;
    position: relative;
  }

  &__image-box {

    @include tablet-880 {
      display: none;
    }
  }

  &__graphic {
    padding-left: 129px;

    @include midle-tablet {
      padding-left: 50px;
    }

    @include mobile {
      padding-left: 0;
    }
  }

  &__list {
    width: 100%;
    padding-top: 50px;
    padding-left: 10px;
    display: flex;
    flex-direction: column;
    position: relative;

    @include small-tablet {
      padding-left: 0;
    }

    &::before {
      content: '';
      width: 2px;
      height: 530px;
      background: #E4E4E4;
      position: absolute;
      top: 0px;
      right: 167px;
      z-index: -1;

      @include tablet-880 {
        height: 470px;
      }
    }
  }

  &__item {
    display: flex;
    align-items: center;
    padding-bottom: 98px;
  }

  &__day {
    white-space: nowrap;
    padding-right: 42px;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    color: var(--grey-color);
  }

  &__dot {
    // margin-right: 24px;
    display: inline-block;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: #228749;
  }

  &__name {
    margin-left: 24px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    color: var(--dark-color);
  }

  &__btn {
    position: absolute;
    bottom: 5px;

  }
}
