.graph-modal {

  &__container {
    width: 800px;
    height: 700px;

    @include tablet-880 {
      width: 100%;
      height: auto;
    }
  }

  &__content {
    padding: 44px;

    @include mobile {
      padding: 20px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;
    padding-bottom: 24px;
    font-weight: 600;
    font-size: 24px;
    line-height: 33px;
    color: #000;
    position: relative;

    &::before {
      content: "";
      background: #e4e4e4;
      width: 100%;
      height: 1px;
      position: absolute;
      left: 0;
      bottom: 0;
    }

    @include mobile {
      font-size: 20px;
    }
  }

  &__close {
    top: 68px;
    right: 65px;
    z-index: 10;


    @include mobile {
      top: 50px;
      right: 50px;
    }
  }
}

.form {
  &__label {
    width: 100%;
  }

  &__span-name {
    margin-bottom: 8px;
    display: block;
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    color: var(--dark-color);
  }

  &__input {
    padding: 24px;
    background: #f9f9f9;
    width: 100%;
    outline: none;
    margin-bottom: 40px;

    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    font-size: 24px;
    line-height: 33px;

    &::placeholder {
      font-family: "Open Sans", sans-serif;
      font-weight: 400;
      font-size: 24px;
      line-height: 33px;
    }

    @include mobile {
      padding: 20px;
      font-size: 18px;
      line-height: 24px;
    }
  }

  &__descr {
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    color: var(--dark-color);
  }

  &__descr-link {
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    color: var(--green-color);
    text-decoration: underline;
  }

  &__btn {
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    text-transform: uppercase;
    color: var(--light-color);

    padding: 24px 64px;
    width: 100%;
    background: #228749;
    border-radius: 4px;

    @include mobile {
      padding: 20px 30px;
    }
  }
}
