.hero {
  padding: 160px 0 110px;

  @include midle-tablet {
    padding: 100px 0 80px;
  }

  @include small-tablet {
    padding: 24px 0 64px;
  }

  &__title {
    margin: 0;
    margin-bottom: 60px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 72px;
    line-height: 88px;
    text-transform: uppercase;
    color: var(--dark-color);

    span {
      color: var(--green-color);
    }

    @include midle-tablet {
      font-size: 52px;
      line-height: 68px;
    }

    @include small-tablet {
      font-size: 32px;
      line-height: 140%;
    }
  }

  &__content {
    max-width: 540px;
    margin-left: auto;
    margin-right: 110px;

    @include small-tablet {
      margin: 0 auto;
      display: flex;
      flex-direction: column;
      width: 100%;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 40px;
    font-weight: 400;
    font-size: 24px;
    line-height: 33px;
    color: var(--dark-color);

    span {
      color: var(--green-color);
    }

    @include small-tablet {
      font-size: 22px;
      line-height: 30px;
    }
  }

  &__btn {
    @include small-tablet {
      padding: 24px 50px;
    }
  }
}
