.nav {
  &__title {
    font-weight: 600;
    font-size: 24px;
    line-height: 33px;
    color: #000000;
    display: none;
    padding-left: 32px;
    padding-top: 30px;

    @include midle-tablet {
      display: block;
    }
  }

  &__list {
    display: flex;

    @include midle-tablet {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100%;
      // padding-top: 50px;
      padding-left: 32px;
      padding-right: 32px;
      padding-bottom: 32px;

      &::before {
        content: "";
        background: #e4e4e4;
        width: 100%;
        height: 1px;
        margin-top: 30px;
      }
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 64px;
    }

    @include big-desktop {
      &:not(:last-child) {
        margin-right: 50px;
      }
    }

    @include midle-tablet {



      &:not(:last-child) {
        margin-bottom: 33px;
      }

      &:first-child {
        margin-top: 30px;
      }
    }
  }

  &__link {
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    color: var(--dark-color);

    @include midle-tablet {
      display: block;
      width: 100%;
      
    }
  }

  .active {
    color: var(--green-color);
    position: relative;
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;

    &::after {
      content: "";
      background: var(--green-color);
      width: 100%;
      height: 3px;
      position: absolute;
      left: 0;
      bottom: -41px;

      @include midle-tablet {
        content: none;
      }
    }
  }

  @include hight-tablet {
    order: -1;
  }

  @include midle-tablet {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 500;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    background: var(--light-color);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
}

.burger-close {
  display: none;

  @include midle-tablet {
    display: inline-block;
    position: absolute;
    width: 24px;
    height: 24px;
    z-index: 5105;
    top: 32px;
    right: 32px;
  }
}

.burger {
  @include burger;

  height: 20px;
  display: none;

  @include midle-tablet {
    display: block;
  }
}
